@import url(https://fonts.googleapis.com/css?family=Noto+Sans+SC&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Shadows+Into+Light+Two&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{background:#f3f7fd;font-family:'Noto Sans SC', sans-serif}.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.separator{display:flex;align-items:center;margin-top:0.5rem;margin-bottom:0.5rem}.separator::before,.separator::after{content:'';flex:1 1;border-bottom:1px solid #afaeae}.separator::before{margin-right:.25em}.separator::after{margin-left:.25em}

.info-body-header{height:70px;line-height:70px;background-color:#282b34;border-bottom:1px solid #f2f4f9;box-shadow:3px 0 10px 0 rgba(183,192,206,0.2);padding-left:20px;padding-right:20px;top:0;z-index:1000}.header-line-height{line-height:70px}.header-name-container{font-size:30px}.header-icon{color:#dd3b1f}.preview-btn{height:44px !important;width:44px !important;background-color:#282b34 !important}.preview-btn-container .active{background-color:#9c9999 !important}

.App-content{min-height:calc(100vh - 70px);padding-bottom:50px;background-color:white}.info-editor-container{margin-top:70px !important}

.infoitem-default{min-height:100px;background-color:#ebeff2;font-family:'Oswald', sans-serif;margin-right:10px}.infoeditor-default{background-color:#ebeff2;font-size:14px !important;margin:2px;border:2px solid #bdbdc0;border-radius:5px}

.info-background{background-color:#dad6d6}.editor-background{background-color:#dad6d6}.input-cosmetics{font-size:12px;width:300px}.btn-cosmetics{font-size:15px;width:35px;padding:0;height:35px;background-color:white;color:#dd3b1f;border:2px solid #dd3b1f}.btn-cosmetics:hover{color:white;background-color:#dd3b1f}

.default-icon{font-size:25px;color:#dd3b1f;cursor:pointer;padding-left:10px;padding-top:2px;border:2px solid #dd3b1f;border-radius:5px;-webkit-transition:all 0.2s ease-in-out;transition:all 0.2s ease-in-out}.default-icon:hover{background-color:#dd3b1f;color:white}

.type-btn{font-size:12px !important;background-color:white;text-transform:capitalize;color:#636161}.color-btn{width:20px !important;height:20px !important}div:focus{outline:0px}h1:focus{outline:0px}input:focus{outline:0px}.header-text{margin-top:0px;margin-bottom:0px}.text-decoration-btn{width:30px;background-color:white;color:#636161}.text-alignment-btn{width:30px;background-color:white;color:#636161}.font-underline{text-decoration:underline !important}

.upload-button{background-color:#ffffff;color:#000000;height:26px !important;padding-top:1px !important;font-size:14px !important;border-color:#bababa}.spreadsheet-input{display:inline-block}

.map-item-icon{height:40px !important;width:56px !important;padding-top:5px !important;font-size:20px !important}.add-remove-btn{height:30px !important;padding-top:0px !important;padding-bottom:10px !important;margin-left:10px;width:40px !important;font-size:18px !important;font-weight:bold !important;border:1px solid #5a6268;color:#5a6268 !important;background-color:white !important}.map-height-range input{width:220px}

.input-file{width:90px}.image-input-number{width:40px}.expand-btn{padding-left:7px;padding-top:2px;width:25px;height:25px;color:#dd3b1f;bottom:24px;right:0px;font-size:14px;cursor:pointer}.expand-btn:hover{font-size:16px}.image-filter{margin-right:10px}.image-filter input{margin-left:auto;margin-right:10px;margin-top:3px;width:230px;background-color:white}

.star-rating{unicode-bidi:bidi-override;color:white;font-size:25px;display:inline-block;position:relative;padding:0}.star-rating-percentage{color:#ffd83e;padding:0;position:absolute;z-index:1;display:inline-block;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out;top:0;left:0;overflow:hidden}.star-rating-background{padding:0;display:inline-block;z-index:0}

.rating{background-color:white}.rating .progress-bar{background-color:#dd3b1f}.rating-editor-btn{width:55px;height:45px;margin-left:5px;background-color:white;color:#5a6268;font-size:20px}.rating-editor-btn .progress-bar{background-color:#5a6268}.rating-editor-input{font-size:14px;height:23px !important;width:75px !important;padding-left:4px}.rating-editor-star-btn{padding-top:0 !important;padding-bottom:0 !important}.rating-container{width:83%;margin-top:-5px}

.settings-panel-container{min-height:100vh;overflow-y:scroll;width:245px;top:0px;-webkit-transition:all 0.2s ease-in-out;transition:all 0.2s ease-in-out}.theme-container{width:150px;height:250px;background-color:olive;border:2px solid #dd3b1f;border-radius:3px;margin:auto;margin-top:15px;cursor:pointer}.theme-container svg{cursor:pointer}.settings-btn{width:45px;height:45px;top:120px;border-top-left-radius:4px;border-bottom-left-radius:4px;background-color:#dd3b1f;z-index:4000;-webkit-transition:all 0.2s ease-in-out;transition:all 0.2s ease-in-out;cursor:pointer}.settings-btn svg{width:20px;height:20px}.theme-demo{font-family:'Oswald', sans-serif}.demo-progress{height:5px}

